import React, { useState } from 'react'
import Alert from './components/Alert';
import Navbar from './components/Navbar';
import Textform from './components/Textform';

export default function App() {
  const [mode, setMode] = useState("light")
  const [navMode, setNavMode] = useState("light")
  const [color, setColor] = useState("light")
  const [bgColor, setBgColor] = useState("light")
  const [alert, setAlert] = useState(null)

  // Alert
  const showAlert = (message, type) => {
    setAlert({
      msg: message,
      type: type
    })
    setTimeout(() => {
      setAlert(null);
    }, 1500);
  }

  const darkMode = () => {
    if (mode === 'light') {
      setMode("dark")
      setNavMode('dark')
      setColor('light')
      setBgColor('#212529')
      showAlert("Dark mode has been elabled","success")
      document.body.style.backgroundColor = "#212529"
    }
    else {
      setMode("light")
      setNavMode("light")
      setColor('light')
      // setBgColor("black")
      showAlert("light mode has been elabled","success")
      document.body.style.backgroundColor = "white"
    }
  }
  const primaryMode = () => {
    if (mode === 'light') {
      setMode("primary")
      setNavMode("dark")
      setColor('light')
      setBgColor('#15202B')
      document.body.style.backgroundColor = "#15202B"
      document.body.style.color = "white"
    }
    else {
      setMode('light')
      setNavMode("light")
      document.body.style.backgroundColor = "white"
      document.body.style.color = "black"
    }
  }
  const dangerMode = () => {
    if (mode === 'light') {
      setMode("danger")
      setNavMode("dark")
      setColor('light')
      setBgColor('#8c4850')
      document.body.style.backgroundColor = "#8c4850"
      document.body.style.color = "white"
    }
    else {
      setMode('light')
      setNavMode("light")
      document.body.style.backgroundColor = "white"
      document.body.style.color = "black"
    }
  }
  const warningMode = () => {
    if (mode === 'light') {
      setMode("warning")
      setNavMode("light")
      setColor('light')
      setBgColor('#e8c869')
      document.body.style.backgroundColor = "#e8c869"
      document.body.style.color = "black"
    }
    else {
      setMode('light')
      setNavMode("light")
      document.body.style.backgroundColor = "white"
      document.body.style.color = "black"
    }
  }

  const modes = [darkMode, primaryMode, dangerMode, warningMode]
  return (
    <>
      <Navbar title="TextUtils" mode={mode} navMode={navMode} darkMode={modes[0]} primaryMode={modes[1]} dangerMode={modes[2]} warningMode={modes[3]} />
      <Alert alert={alert} />
      <Textform heading="Enter the Text" showAlert={showAlert} mode={mode} color={color} bgColor={bgColor} primaryMode={modes[1]} dangerMode={modes[2]} warningMode={modes[3]} />
    </>
  )
}
