import React, { useState } from 'react'

export default function Textform(props) {
    const [text, setText] = useState("")

    const converToUpperCase = () => {
        let newText = text.toUpperCase();
        setText(newText)
        props.showAlert("Converted to upper Case", "success")
    }
    const converToLowerCase = () => {
        let newText = text.toLowerCase()
        setText(newText)
        props.showAlert("Converted to lower Case", "success")
    }
    const copyText = () => {
        let newText = document.getElementById("myform")
        newText.select()
        navigator.clipboard.writeText(newText.value)
        props.showAlert("Copied to clipboard", "success")
    }
    const ClearSpaces = () => {
        let newText = text.split(/[ ]+/)
        setText(newText.join(" "))
        props.showAlert("Cleared extra spaces", "success")

    }
    const onChangeHandler = (event) => {
        console.log("on change");
        setText(event.target.value)
    }

    return (
        <div className="container" >
            <div className="mb-3" style={{ color: props.mode === "light" ? "#212529" : "white" }}>
                <h2 className="my-3" >{props.heading}</h2>
                <textarea className="form-control" id="myform" value={text} onChange={onChangeHandler} style={{ backgroundColor: props.mode === props.color ? "white" : props.bgColor, color: props.mode === "light" ? "#212529" : "white" }} rows="6"></textarea>
                <button className="btn btn-primary my-3 me-2" onClick={converToUpperCase}>Conver to UpperCase</button>
                <button className="btn btn-primary my-3 me-2" onClick={converToLowerCase}>Conver to LowerCase</button>
                <button className="btn btn-primary my-3 me-2" onClick={copyText}>Copy Text</button>
                <button className="btn btn-primary my-3" onClick={ClearSpaces}>Remove Extra Spaces</button>
            </div>
            {/* outer element */}
            <div className="container" style={{ color: props.mode === "light" ? "#212529" : "white" }}>
                <h3>{text.length} letters and {text.split(" ").length} words</h3>
                <p>{0.008 * text.split(" ").length} minutes to read</p>
                <h3>Preview</h3>
                <p>{text.length > 0 ? text : "Enter the text to preview"}</p>
            </div>
        </div>
    );
}