import React from 'react'

export default function Navbar(props) {
    
    return (
        <>
            <nav className={`navbar navbar-expand-lg navbar-${props.navMode} bg-${props.mode}`}>
                <div className="container-fluid">
                    <a className="navbar-brand" href="/">{props.title}</a>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="/navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                                <a className="nav-link active" aria-current="page" href="/">Home</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/">Link</a>
                            </li>
                        </ul>
                        {/* Radio button */}
                        <button type="button" className="btn btn-primary me-2" onClick={props.primaryMode}></button>
                        <button type="button" className="btn btn-danger me-2" onClick={props.dangerMode}></button>
                        <button type="button" className="btn btn-warning me-2" onClick={props.warningMode}></button>
                        <div className="form-check form-switch" onClick={props.darkMode}>
                            <input className="form-check-input" type="checkbox" id="darkMode" />
                            <label className="form-check-label" htmlFor="darkMode" style={{color:props.mode==='light'?'black':'white'}}>Enable Dark Mode</label>
                        </div>
                    </div>
                </div>
            </nav>
        </>
    );
}